.statistics {
  width: 30%;
  height: 100%;
  background-color: #fff;
}
.title {
  text-align: center;
  padding-top: 30px;
  font-weight: 600;
  color: grey;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}
.list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left: -40px;
  list-style: none;
  padding-top: 50px;
}
.list > li {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #bada55;
  width: 100%;
  height: 100%;
  text-align: center;
  padding: 10px;
  color: #fff;
}
.list > li:nth-child(2n + 2) {
  background-color: lightskyblue;
}
.list > li:first-child {
  background-color: #d32f2f;
}
.list > li:last-child {
  background-color: #d32f2f;
}
.list > li:nth-child(3n) {
  background-color: slateblue;
}

.num {
  font-size: 20px;
}
